'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import Button from '@/components/button';
import Link from 'next/link';

export default function ForgotPassword() {
    const [email, setEmail] = useState('');
    const [message, setMessage] = useState('');
    const [error, setError] = useState('');
    const router = useRouter();

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setError('');

        try {
            const response = await fetch('/api/users/forgot-password', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ email }),
            });

            const data = await response.json();

            if (response.ok) {
                setMessage(data.message);
            } else {
                setError(data.message || 'Error al enviar la solicitud');
            }
        } catch (err) {
            setError('Error al enviar la solicitud. Intenta de nuevo.');
            console.error(err);
        }
    };

    return (
        <main className="flex h-screen items-center justify-center">
            <div className="w-full max-w-md p-8 bg-white rounded-lg shadow-lg">
                <h1 className="font-bold text-2xl mb-6 text-center">Recuperar contraseña</h1>

                <form onSubmit={handleSubmit} className="space-y-6">
                    <div>
                        <label className="font-bold">Correo</label>
                        <input
                            type="email"
                            placeholder="Escribe aquí tu correo"
                            className="w-full px-4 py-3 border rounded-lg bg-gray-50 focus:outline-none focus:ring-2 focus:ring-green-500"
                            value={email}
                            onChange={(e) => setEmail(e.target.value)}
                            required
                        />
                    </div>

                    {message && <p className="text-green-500 text-center">{message}</p>}
                    {error && <p className="text-red-500 text-center">{error}</p>}

                    <Button
                        icon="/loginButton.svg"
                        iconWidth={500}
                        iconHeight={64}
                        bgColor="bg-transparent"
                        border="border-none"
                        type="submit"
                    />

                    <div className="text-center space-y-4">
                        <Link href="/login" className="text-gray-400 hover:underline block">
                            Volver al inicio de sesión
                        </Link>
                    </div>
                </form>
            </div>
        </main>
    );
}